import React, { useState, useContext,useEffect, useRef } from "react";
// import "./ReportProperties.css"
import { Card, Row, Col,Form } from "react-bootstrap";
import { AppContext } from "../../../../Resources/CanvasContextFile";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import ReportPropertiesJson from "../../../Properties/ReportPropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import ImageUploadBtn from "../../../../ReusableComponents/ImageUploadBtn/ImageUploadBtn";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import SvgDeleteIcon from "../../../../Icons/DeleteIcon";
// import StyleJSON from "./MultiSelectPropertiesJSON"
import SvgStyle1 from "../../../../Icons/Radio/style1";
import SvgStyle2 from "../../../../Icons/Radio/style2";
import SvgStyle3 from "../../../../Icons/Radio/style3";
import PlusOrange from "../../../../Icons/PlusOrange";
import RedCross from "../../../../Icons/RedCross";
import ActiveIcon from "../../../../ReusableComponents/ActiveIcon/ActiveIcon";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import InputBase from "@material-ui/core/InputBase";
import Divider from "@material-ui/core/Divider";
import DOTS from "../../../../Icons/ThreeDots.jsx";
import Cross from "../../../../Icons/Cross.jsx";
import ModalItem from "../../../../UI/BasicFormControls/Report/ReportModal";
import Modal from "@material-ui/core/Modal";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import {useDispatch,addContexts,useSelector} from "../../../../Redux";
import ReportModal from "../../../../UI/BasicFormControls/Report/ReportModal";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks.js";

function ReportProperties(props) {
  const contexts = GetStateFromData(props.containerId);
	  const {updateJson, changeSectionLayout, formState2, deleteControl,setQuickToolsClose,customControlIdList,setCustomControlIdList }=contexts.contexts;
	  const { updatedComponentInFormState, removeAndUpdateComponentInFormState} =updateJson;


  // const { t } = useTranslation();
  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA"; 
  const { t } = useTranslation(['styles', 'translation']);
  // const AppContextVar = useContext(AppContext);
  // const { updateJson, deleteControl } = useContext(AppContext);
  // const { updatedComponentInFormState, removeAndUpdateComponentInFormState } =
  //   updateJson;
  const style = props.selectedControl.style;
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});
  const [filter, setFilter] = useState( props?.selectedControl?.style?.reportName||"");
  const [open, setOpen] = React.useState(false);
  const [inputBoxId, setInputBoxId] = React.useState(false);
  const iFrameSrcRef = useRef("")
  var CollapsibleItems = [];

  const handleOpen = () => {
    let iframe = document.getElementById(props.selectedControl.id);
    iFrameSrcRef.current = iframe.src
    setOpen(true);
  };

  const handleClose = (report_name,initiator) => {
    setOpen(false);
    if (report_name && typeof report_name==='string') {
      
      props.selectedControl.style.reportName = report_name;
      updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
      setFilter(report_name);
    }
    if(initiator === "cancel"){
      let iframe = document.getElementById(props.selectedControl.id);
      iframe.src = iFrameSrcRef.current;
    }
  };

  
useEffect(()=>{
 setFilter(props?.selectedControl?.style?.reportName||"")
},[props?.selectedControl?.style])
  Object.keys(ReportPropertiesJson).forEach((key, index) => {
    var collapseComponentBody = [];

    Object.keys(ReportPropertiesJson[key]).forEach((property) => {
      const fetchedProperty = property.replace(/\d+/g, "");
      //used to remove any numbers from property
      if (
        props.selectedControl.style !== undefined &&
        props.selectedControl.style["ControlStyle"] !== undefined &&
        props.selectedControl.style["ControlStyle"] !== null &&
        props.selectedControl.style["ControlStyle"] === "style3"
      ) {
        if (property !== "ColorPicker4") {
          collapseComponentBody.push(
            returnHtml(
              fetchedProperty,
              ReportPropertiesJson[key][property],
              ControlsMap[fetchedProperty].type
            )
          );
        }
      } else {
        if (
          property !== "ColorPicker5" &&
          property !== "ColorPicker6" &&
          property !== "ColorPicker7" &&
          property !== "ColorPicker8"
        ) {
          collapseComponentBody.push(
            returnHtml(
              fetchedProperty,
              ReportPropertiesJson[key][property],
              ControlsMap[fetchedProperty].type
            )
          );
        }
      }
    });
    CollapsibleItems.push(
      <CollapsibleComponent
        id={index + ""}
        heading={key}
        body={collapseComponentBody}
      />
    );
  });

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      // console.log(ControlsMap[propertyName]["regexType"]);
      // console.log(String.fromCharCode(event.which));
      // console.log(regexValidator(String.fromCharCode(event.which), ControlsMap[propertyName]["regexType"]));
      let passValue=event?.key;
      if(propertyName==="CustomId"){
        passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !(event.which >= 96 && event.which <= 105) &&
        !regexValidator(
          passValue,
          ControlsMap[propertyName]["regexType"]
        )
      ) {
        event.preventDefault();
        // alert("in regex func");
        setErrorMessageId(inputBoxId);
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setErrorMessageId(false);
      }
    } catch (e) {
       console.log("Error while validating regex");
    }
  }

  function handleProperty(propertyName, propertyValue, event, inputBoxId) {
    try {
      // if(event && !(regexValidator( propertyValue, ControlsMap[propertyName]["regexType"]))){
      //     event.preventDefault();
      //     //setErrorMessageId(inputBoxId);
      //     setAlertData(
      //         {...ControlsMap["customizedAlertNotificationData"], 'open' : true, 'alertContentKey':'RegexErrorMessage'});
      // }

      if(propertyName === "Height" && parseFloat(propertyValue) < 150) {
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "Height cannot be less than 150px",
        });
        return;
      }

      let temp_style = props.selectedControl.style;
      temp_style[propertyName] = propertyValue;
      props.selectedControl.style[propertyName] = propertyValue;
      updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
      // setErrorMessageId(false);
    } catch (e) {}
  }

  function handleStyleType(property, value) {
    // console.log(value);
    // console.log(property);
    handleProperty(property, value);
  }
  function handleSupProperty(propertyName, propertyValue, event, inputBoxId) {
    try {
      if (
        event &&
        !regexValidator(propertyValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setErrorMessageId(inputBoxId);
      } else {
        if (propertyName === "label") {
          props.selectedControl[propertyName] = propertyValue;
        } else {
          props.selectedControl.style[propertyName] = propertyValue;
        }
        updatedComponentInFormState(
          props.selectedControl.id,
          props.selectedControl
        );
        setErrorMessageId(false);
      }
    } catch (e) {}
  }
  const updateEventData = (eventJson) => {
		// console.log("===>Received Event Json :: ", eventJson);
		// console.log("===>Received props.selectedControl :: ", props.selectedControl);
		var count = Object.keys(eventJson).length;
		if(count>0){
			var Events = {};
			Events["Events"] = eventJson;
			props.selectedControl["event"] = Events;
		}
		// console.log("===> props.selectedControl", props.selectedControl);
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
	}

  function addOptionsFunc() {
    const oldOptionsArr = props.selectedControl.options || [];
    const newOptionsArr = [...oldOptionsArr, { label: "", value: "" }];
    props.selectedControl.options = newOptionsArr;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function handleOptions(e, index) {
    const { name, value } = e.target;
    const oldOptionsArr = props.selectedControl.options || [];
    var changes;
    if (
      name === "label" &&
      oldOptionsArr[index].value === oldOptionsArr[index].label
    ) {
      changes = { ...oldOptionsArr[index], [name]: value, value: value };
    } else {
      changes = { ...oldOptionsArr[index], [name]: value };
    }

    oldOptionsArr[index] = changes;
    props.selectedControl.options = oldOptionsArr;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function handleIcon(iconName, iconFile) {
    const temp_style = style;
    temp_style["iconName"] = iconName;
    temp_style["iconFile"] = iconFile;
    props.selectedControl.style["iconName"] = iconName;
    props.selectedControl.style["iconFile"] = iconFile;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    props.selectedControl.style[field] = value;
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  // function handleInputRadio(e,field){
  //     const value=e.target.value;shortinput
  //     props.selectedControl.style[field] = value;
  //     console.log(value, field);
  //     updatedComponentInFormState(props.selectedControl.id , props.selectedControl)
  // }
  function deleteOption(index) {
    const optionsArr = props.selectedControl.options;
    optionsArr.splice(index, 1);
    props.selectedControl.options = optionsArr;
    // console.log(optionsArr);
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function handleDelete(){
    setFilter("");
    props.selectedControl.style.reportName = ""
    delete props.selectedControl.selectId
    let iframe = document.getElementById(props.selectedControl.id);
    iframe.removeAttribute("src")
    updatedComponentInFormState(props.selectedControl.id,props.selectedControl)
  }

  function returnHtml(key, value, type) {
    switch (type.toLowerCase()) {
      case "toggle":
        return (
          <div
            className={
              value.field === "showIcon" ? "customToggleBtn1" : "toggleBtn"
            }
          >
            <span> {t(`translation:${ControlsMap[key].label}`)} </span>
            {/* <ToggleBtn
              handleToggle={handleToggle}
              field={value.field}
              toggleState={style[value.field]}
            /> */}
          </div>
        );
      case "toggletype2":
        return (
          <div
            className={
              value.field === "showIcon" ? "customToggleBtn1" : "toggleBtn"
            }
          >
            <span> {t(`translation:${value.label}`)} </span>
            <ToggleBtn
               key={props.selectedControl.id}
              handleToggle={handleToggle}
              field={value.field}
              toggleState={style[value.field]}
            />
          </div>
        );
      case "combo":
        return (
          <div className="combo">
            {t(`translation:${ControlsMap[key].label}`)}
            <select
              style={{ width: value.field === "zonelist" ? "121px" : null }}
              value={style[value.field]}
              onChange={(e) => handleProperty(value.field, e.target.value)}  
              key={props.selectedControl.id}
              // defaultValue={key === "BorderRadius"?"2px" : "12px"}           
            >
              {ControlsMap[key].children.map((item) => {
                return <option value={item.Value} style={item?.style}>  {t(`translation:${item.Title}`)} </option>;
              })}
            </select>
          </div>
        );
      case "select":
        return (
          <div className="combo">
            {t(`translation:${value.label}`)}
            <select
              style={{ width: value.field === "zonelist" ? "121px" : null }}
              value={style[value.field]}
              onChange={(e) => handleProperty(value.field, e.target.value)}
              key={props.selectedControl.id}
            >
              {ControlsMap[key].children.map((item) => {
                return <option value={item.Value}>   {t(`translation:${item.Title}`)} </option>;
              })}
            </select>
          </div>
        );

        case "text":
          return (
            <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
              <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
              <div className={styles.RightTextBox}>
                <input
                  aria-label={"Control Value1"}
                  type="text"
                  defaultValue={props.selectedControl.style[key].replace(/\D/g,'')}
                  key={props.selectedControl.id}
                  className={styles.RightLabelTextBox}
                  onKeyPress={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onBlur={(event) =>
                    handleProperty(key, event.target.value, event)
                  }
                />
                {key !== "CustomId" ? (
                  <div className={translationStyles1 ? styles.RightTextboxPlaceholderArabic : styles.RightTextboxPlaceholder} style={{top: "-22px"}}>px</div>
                ) : null}
              </div>
            </div>
          );
      case "line":
        return (
          <hr
            className="line"
            size={value.size}
            color={value.color}
            width={value.width}
          />
        );
      case "colorpicker":
        return (
          <div className="colorpicker">
            {t(`translation:${value.label}`)}
            <ColorPicker
               key={props.selectedControl.id}
              color={style[value.field]}
              handleProperty={handleProperty}
              propertyName={value.field}
            />
          </div>
        );

        case "textbox":
          return (
            <div>
              <div className={styles.leftLabelForTextBox}>
                <div className={styles.labelForLabel} >{t(`translation:${ControlsMap[key].label}`)} </div>
                <input 
                  aria-label={"Control Value2"}
                  type="text"  
                  defaultValue={props.selectedControl.style[key]} 
                  key={props.selectedControl.id}
                  id={props.selectedControl.id+key}
                  className={[styles.textBoxLayoutForLable, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
                  onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onBlur={(event)=>handleProperty(key, event.target.value, event)}
                />
              </div>
              {errorMessageId === props.selectedControl.id + key ? (
                <ErrorMessageComponent errorMessage="non-permissible value" />
              ) : null}
            </div>
          );
          
      case "textarea":
        return (
          <div className="textarea">
            {t(`translation:${value.label}`)}
            <input
              aria-label={"Control Value3"}
              autoComplete="off"
              rows="2"
              defaultValue={
                value.field === "label"
                  ? props.selectedControl[value.field]
                  : props.selectedControl.style[value.field]
              }
              key={props.selectedControl.id}
              className={[
                errorMessageId === props.selectedControl.id + key
                  ? styles.inputBoxErrorStyle
                  : null,
              ].join(" ")}
              onKeyPress={(e) =>
                handleSupProperty(
                  value.field,
                  e.target.value,
                  e,
                  props.selectedControl.id + key
                )
              }
              onKeyUp={(e) =>
                handleSupProperty(
                  value.field,
                  e.target.value,
                  e,
                  props.selectedControl.id + key
                )
              }
            />
          </div>
        );
      case "input":
        return (
          <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
          <span className={styles.leftLabel}>
           {t(`translation:${value.label}`)}
          </span>
          <div className={styles.RightLabelSelectDiv1}>
              <div className={styles.TableHeightinput}>
                  <span className={styles.RightLabelTextInputSpanReport}>px</span>
                  <input aria-label={"Control Value4"} type="text" 
                   className={styles.RightLabelTextInputTable}
                   defaultValue={props.selectedControl.style[value.field]}
                   key={props.selectedControl.id}
                   onKeyPress={(event) =>
                                          validateRegex(
                                            key,
                                            event.target.value,
                                            event,
                                            props.selectedControl.id + key
                                          )
                                        }
                                        onKeyUp={(event) =>
                                          validateRegex(
                                            key,
                                            event.target.value,
                                            event,
                                            props.selectedControl.id + key
                                          )
                                        }
                                        onBlur={(e) => handleProperty(value.field, e.target.value)}  
                      />  
              </div>
           </div>
          </div>

          // <div className="input"    >
          //    {/* <div className={styles.reportInput} > */}
          //   {/* className={styles.parentDiv} */}
          //   {t(`translation:${value.label}`)}
          //   <input
          //     onKeyPress={(event) =>
          //       validateRegex(
          //         key,
          //         event.target.value,
          //         event,
          //         props.selectedControl.id + key
          //       )
          //     }
          //     onKeyUp={(event) =>
          //       validateRegex(
          //         key,
          //         event.target.value,
          //         event,
          //         props.selectedControl.id + key
          //       )
          //     }
          //     onBlur={(e) => handleProperty(value.field, e.target.value)}
          //     autoComplete="off"
          //   />
          // </div>
        );
      case "longinput":
        return (
          <div className="longinput">
            {t(`translation:${value.label}`)}
            <input aria-label={"Control Value5"} autoComplete="off" />
          </div>
        );
      case "uploadbtn":
        return (
          <>
            {props.selectedControl.style.showIcon &&
              props.selectedControl.style.showIcon === "true" && (
                <div className="uploadbtn">
                  <ImageUploadBtn
                  containerId={props.containerId}
                    handleImageUpload={handleIcon}
                    iconName={props.selectedControl.style.iconName}
                  />
                </div>
              )}
          </>
        );
      case "inputcheckbox":
        return (
          <div className="inputcheckbox">
            <input aria-label={"Control Value6"} type="checkbox" /> <span> &nbsp; {value}</span>
          </div>
        );
      case "inputradio":
        return (
          <div className="inputradio">
            {value.label.map((item) => {
              {
                /* console.log(value.label); */
              }
              return (
                <div>
                  <input
                    aria-label={"Control Value7"}
                    type="radio"
                    id={item.name}
                    name={value.field}
                    checked={item.value === style.ComboType}
                    value={item.value}
                    onClick={(e) => handleToggle(e, value.field)}
                  />
                  <label aria-label={item.name} htmlFor={item.name}> &nbsp; {t(`translation:${item.name}`)}</label>
                </div>
              );
            })}
          </div>
        );
      case "styleoptions":
        return (
          <ActiveIcon
            activeStyle={{ width: "80px" }}
            layout="3"
            defaultValue="style2"
            inActiveStyle={{ width: "80px" }}
            spacing="4px"
            passedValue={style.ControlStyle}
            property={value.field}
            handleStyleType={handleStyleType}
          >
            <SvgStyle1 value="style1" label="Style1" />
            <SvgStyle2 value="style2" label="Style2" />
            <SvgStyle3 value="style3" label="Style3" />
          </ActiveIcon>
        );
      case "cardlabelvalue":
        return (
          <>
            {props.selectedControl.options &&
              props.selectedControl.options.map((item, count) => {
                return (
                  <div className="cardlabelvalue">
                    <p>{t(`translation:${"Option Label"}`)} {count + 1}</p>
                    <input
                      aria-label={"Control Value8"}
                      autoComplete="off"
                      placeholder={item.label}
                      defaultValue={item.label}
                      onBlur={(e) => handleOptions(e, count)}
                      name="label"
                    />
                    <span onClick={() => deleteOption(count)}>
                      {" "}
                      <RedCross />{" "}
                    </span>
                    <p>{t(`translation:${"Option Value"}`)}  </p>
                    <input
                      aria-label={"Control Value9"}
                      autoComplete="off"
                      placeholder={item.value}
                      defaultValue={item.value}
                      onBlur={(e) => handleOptions(e, count)}
                      name="value"
                      key={props.selectedControl.id}
                    />
                  </div>
                );
              })}
          </>
        );
      case "widebutton":
        return (
          <>
            {
              <div
                className="widebutton"
                style={{
                  border: `1px solid ${value.color}`,
                  color: value.color,
                }}
                onClick={addOptionsFunc}
              >
                {" "}
                <PlusOrange color={value.color} /> &nbsp; {t(`translation:${value.label}`)}{" "}
              </div>
            }
          </>
        );
      case "optionsselect":
        return (
          <ActiveIcon
            activeStyle={{
              padding: "10px",
              background: "#fff",
              margin: "0",
            }}
            property={value.field}
            inActiveStyle={{
              padding: "10px",
              background: "#fff",
              margin: "0",
            }}
            passedValue={style.addOptionsMethod}
            handleStyleType={handleStyleType}
          >
            <span style={{ margin: "0 8px 0 8px" }} value="manually">
              Create Manually
            </span>
            <span value="database">Fetch from Database</span>
          </ActiveIcon>
        );
      case "databaseQuery":
        return <></>;
      case "picklist":
        return (
          <Form.Group className={styles.parentDivTopBottom}>
          <div
            className="text"
            style={{ color: value.color, fontWeight: value.weight }}
          >
            <span className={styles.topBottomLabel}>  {t(`translation:${value.label}`)}</span>
            </div>
            <div
            style={{display:"flex"}}
            className={styles.reportPicklist}
              // style={{
              //   border: "1px solid #c4c4c4",
              //   height: "24px",
              //   display: "flex",
              // }}

            >
              <input
              aria-label={"Control Value11"}
              // inputProps={{
              //   "aria-label":"Control Value1"
              // }}
              //   style={{ 
              //   height: "24px", 
              //   width: "239px", 
              //   paddingLeft:"5px", 
              //   fontSize:"12px" 
              // }}
               className={styles.inputPicklistReport}
                // placeholder="Search"
                value={filter || props.selectedControl.style.reportName}
                disabled
                onChange={(e) => setFilter(e.target.value)}
              />
              {props.selectedControl?.style?.reportName === "" ? (
                ""
              ) : (
                <button
                  // style={{ background: "none", border: "none" }}
                  className={styles.buttonReport}
                  onClick={() => {
                    handleDelete()
                  }}
                >
                  <Cross
                    // style={{ marginTop: "6px", marginRight: "6px" }}
                    className={styles.crossReport}
                  ></Cross>
                </button>
              )}

              <Divider orientation="vertical" />

              <button
                // style={{ background: "none", border: "none" }}
                className={styles.buttonReport}
                onClick={() => {
                  handleOpen();
                }}
              >
                <DOTS
                //  style={{ width: "36px", height: "4px" }}
                 className={styles.dotsReport}
                 ></DOTS>
              </button>
              <Modal open={open} onClose={handleClose}>
                {/* {body} */}
                {/* <ModalItem closeModal={handleClose} data={props.data}/> */}

                <ReportModal closeModal={handleClose}  data={props.selectedControl} 
                containerId={props?.containerId}
                  // selectedReportId={selectedReportId} setSelectedReportId={setSelectedReportId} 
                  // sessionIdConnection={sessionIdConnection} setSessionIdConnection={setSessionIdConnection}
                  // data={data} setData={setData} 
                  //  enableNext={enableNext}  setEnableNext={setEnableNext}
                  // enablePrev={enablePrev} setEnablePrev={setEnablePrev}
                  // options={options}  setOptions={setOptions}
                  />





              </Modal>
            </div>
         
          </Form.Group>
        );
      case "eventbutton":
        return (
          <Events
          key={props.selectedControl.id + "_eventsProperty"}
          json={EventListjson}
          value={props.selectedControl.event}
          elemtype={props.selectedControl.elem_type}
          updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
    }
  }
 

  return (
    <>
      <ControlLabel
        label="Report"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId = {props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        {CollapsibleItems.map((item, index) => {
          return item;
        })}
      </div>
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} key={inputBoxId}/>
            }
    </>
  );
}

export default ReportProperties;
